import "./style.css"

const Banner = ({item}) => {

  console.log(item);

  let genres = []

  for(var i in item.genres){
    genres.push( item.genres[i].name )
  }

  return (
    <section className="banner" 
      style={{
        backgroundImage:`url(https://image.tmdb.org/t/p/w500${item.backdrop_path})`,
        backgroundPosition: "center",
        backgroundSize: "cover"
      }}
    >
      <div className="banner--vertical">
        <div className="banner--herzontal">
          <div className="banner-text">
            <h1>{item.original_name}</h1>
            <div className="banner--info">
              <div className="banner--point">{item.vote_average} points</div>
              <div className="banner--seasons">{item.number_of_seasons} season{item.number_of_seasons > 1 && "s"}</div>
            </div>
            <div className="banner--description">{item.overview}</div>
            <div className="banner--btns">
              <a href="#">â–¶ Play</a>
              <a href="#">+ Add to List</a>
            </div>
            <div className="banner--genres"><strong>Genres:</strong> {genres.join(", ")}</div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Banner;