import { useEffect, useState } from "react";
import Tmbd from "../../api/Tmbd";
import Banner from "../../components/banner";
import MoviesList from "../../components/moviesList";
import Navbar from "../../components/nav";
import "./style.css"

function Browse() {

  const [movieList, setMovieList] = useState([])
  const [banner, setBanner] = useState()

  useEffect( () => {
    const getData = async () => {
      const res = await Tmbd.getHomeList()
      setMovieList(res)

      //get bannner
      let orignals = res.filter( e => e.name === "orignal")[0]
      let randomChoice = Math.floor(Math.random() * (orignals.items.results.length -1))

      const bannerData = await Tmbd.getShowInfo(orignals.items.results[randomChoice].id, "tv")
      setBanner(bannerData)
    }
    getData()
  }, [])

  return (
    <div className="browser">
      <Navbar />
      {banner && <Banner item={banner} />}
      <section className="lists">
        {movieList.map(
          list => <MoviesList list={list} key={list.name} />
        )}
      </section>
    </div>
  );
}

export default Browse;