const API_KEY = "57df6eb100182a195f327b3dbd142a27"
const API_BASE = "https://api.themoviedb.org/3"

const basicFetch = async (endBase) => {
  const req = await fetch(`${API_BASE}${endBase}`)
  const json = await req.json();
  return json;
}
// eslint-disable-next-line
export default {
  getHomeList : async () => [
    {
      name:"orignal",
      title:"Origenal from Netflix",
      items : await basicFetch(`/discover/tv?api_key=${API_KEY}`)
    },
    {
      name:"trending",
      title:"Trending",
      items : await basicFetch(`/trending/all/week?api_key=${API_KEY}`)
    },
    {
      name:"top",
      title:"Top Rated",
      items : await basicFetch(`/movie/top_rated?api_key=${API_KEY}`)
    },
    {
      name:"animation",
      title:"Animation",
      items : await basicFetch(`/discover/movie?api_key=${API_KEY}&with_genres=16`)
    },
  ],
  getShowInfo : async(showId, type) => {
    let info = {};

    switch (type) {
      case "movie":
        info = await basicFetch(`/movie/${showId}?api_key=${API_KEY}`)
        break;

      case "tv":
        info = await basicFetch(`/tv/${showId}?api_key=${API_KEY}`)
        break;
    
      default:
        break;
    }

    return info;
  }
}